// Colors
// ---------------------------------

// Default

$color-default-black:           #000000;
$color-default-white:           #ffffff;
$color-transparent:             rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-nero:                    #292929;
$color-grey:                    #838383;
$color-white-smoke:             #f1f1f1;
$color-white-smoke-2:           #f2f2f2;
$color-snow:                    #f9f9f9;
$color-light-gray:              #d3d3d3;
$color-nobel:                   #999999;
$color-whisper:                 #e9e9e9;
$color-silver:                  #bdbdbd;
$color-matterhorn:              #4f4f4f;
$color-gainsboro:               #e0e0e0;
$color-gainsboro-2:             #d9d9d9;
$color-oxley:                   #659377;
$color-cosmos:                  #ffd7d7;
$color-contessa:                #ce7070;
$color-dim-gray:                #707070;

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default: 16px;

$ff-open-sans: "OpenSans", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$tf-ease-out:                       ease-out;
$tf-ease-in:                        ease-in;
$tf-ease-in-out:                    ease-in-out;
$trans-default:                     0.3s $tf-default;
$trans-ease-out:                    0.3s $tf-ease-out;
$trans-ease-in:                     0.3s $tf-ease-in;
$trans-ease-in-out:                 0.3s $tf-ease-in-out;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;
$trans-600-ease-out:                0.6s $tf-ease-out;
$trans-600-ease-in-out:             0.6s $tf-ease-in-out;
$trans-400-ease-in-out:             0.4s $tf-ease-in-out;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-620:                            620px;
$vp-640:                            640px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1600:                           1600px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
